@charset "UTF-8";
:root {
  --convas-line-color: blueviolet;
  --anchor-color: #85cbce;
  --clip: left center;
  --size: 0;
  --background-thumb: #2b3769;
  --scrollbar-thumb-bgcolor: #757575;
  --shadow-size: 0px 0px 10px 1px rgba(0, 0, 0, 0.4);
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

.gradient-preloader {
  background: linear-gradient(90deg, #2b3769 -23.739%, #93a9ff 96.778%);
}

@media (min-width: 1440.99px) {
  .underline:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline:hover::before {
    width: 47%;
  }
  .underline:hover::after {
    width: 47%;
  }
  .underline::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline-w:hover::before {
    width: 47%;
  }
  .underline-w:hover::after {
    width: 47%;
  }
  .underline-w::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
html {
  height: 100%;
  font-style: normal;
  font-size: 62.5%;
  font-weight: 400;
  font-family: "averta-regular", sans-serif;
  background: linear-gradient(#000 70%, #1f222e);
  counter-reset: heading;
  color: #fff;
  height: 100%;
}
@media screen and (max-width: 1440px) {
  html {
    font-size: 50%;
  }
}

body {
  overscroll-behavior: none;
  font-size: 1.6rem;
  font-weight: 400;
}
@media screen and (max-width: 690px) {
  body.no-scroll {
    overflow: hidden;
  }
}
body.no-scroll {
  overflow: hidden;
}

* {
  padding: 0;
  margin: 0;
  border: none;
  scrollbar-width: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img,
svg {
  vertical-align: top;
  max-width: 100%;
  height: auto;
  pointer-events: none;
}

a {
  text-decoration: none;
  cursor: pointer;
  color: inherit;
}

a:hover {
  text-decoration: none;
}

a:visited {
  vertical-align: middle;
  text-emphasis-style: none;
}

input,
button,
textarea {
  line-height: 100%;
  border: none;
}

button,
input[type=submit] {
  display: inline-block;
  box-shadow: none;
  background-color: transparent;
  cursor: pointer;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input:focus,
input:active,
button:focus,
button:active {
  outline: none;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0.5rem;
}

label {
  cursor: pointer;
}

a:focus,
a:active,
input:focus,
textarea:focus {
  outline: none;
}

ul,
ol,
li {
  list-style-type: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-style: inherit;
}

::selection {
  color: #9b9999;
  background-color: rgba(0, 84, 99, 0.2470588235);
  text-shadow: 0 1px 0 #000;
}

::-moz-selection {
  color: #9b9999;
  background-color: rgba(0, 84, 99, 0.2470588235);
  text-shadow: 0 1px 0 #000;
}

::-webkit-scrollbar {
  width: 0.5rem;
  padding: 0.031em;
}

::-webkit-scrollbar-track {
  background: var(--scrollbar-thumb-bgcolor);
  background: transparent;
}

::-webkit-scrollbar-thumb {
  border: 1px solid var(--border-color);
  border-radius: 2px;
  background-color: var(--themes-color);
  background-image: -webkit-linear-gradient(45deg, var(--background-thumb) 25%, transparent 25%, transparent 50%, var(--background-thumb) 50%, var(--background-thumb) 75%, transparent 75%, transparent);
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--background-thumb);
  background-image: -webkit-linear-gradient(45deg, var(--themes-color) 25%, transparent 25%, transparent 50%, var(--themes-color) 50%, var(--themes-color) 75%, transparent 75%, transparent);
}

.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  height: 100%;
}

[class*=_container] {
  max-width: 1920px;
  padding: 0 15px;
  margin: 0 auto;
  height: 100%;
  box-sizing: content-box;
}

[class*=icon-] {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateX(0.5px);
}
[class*=icon-]::before {
  pointer-events: none;
}

[class*=icon-arrow-]::before {
  font-size: 2.4rem;
}
@media (min-width: 1440.98px) and (max-width: 1920px) {
  [class*=icon-arrow-]::before {
    font-size: calc(15px + 9 * (100vw - 1440px) / 480);
  }
}
@media screen and (max-width: 960px) {
  [class*=icon-arrow-]::before {
    font-size: 2.2rem;
  }
}
@media screen and (max-width: 490px) {
  [class*=icon-arrow-]::before {
    font-size: 1.8rem;
  }
}
@media screen and (max-width: 375px) {
  [class*=icon-arrow-]::before {
    font-size: 1.6rem;
  }
}

@keyframes shaking {
  0% {
    transform: translateY(0);
  }
  25% {
    transform: translateY(5px);
  }
  50% {
    transform: translateY(-5px);
  }
  75% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes shake {
  10%, 90% {
    transform: translateX(-0.5px);
  }
  20%, 80% {
    transform: translateX(1px);
  }
  30%, 50%, 70% {
    transform: translateX(-2px);
  }
  40%, 60% {
    transform: translateX(2px);
  }
}